module.exports = "export function init (cornify) {\n  const konamiCode = 'konami'\n  const pressed    = []\n  window.addEventListener('keyup', e => {\n    pressed.push(e.key)\n    pressed.splice(-konamiCode.length - 1, pressed.length - konamiCode.length)\n\n    if (pressed.join('').includes(konamiCode)) {\n      cornify.cornify_add()\n    }\n  })\n}\n"